.navigation {
  width: 43.22vw;

  @include mobile {
    display: none;
  }  

  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }

  &__li {

  }

  &__link {
    font-size: 0.93vw;
    line-height: 0.93vw;
    font-weight: 500;
    display: flex;
    align-items: center;
    text-transform: uppercase;
    color: #333;
  }

  &__icon {
    width: 1.04vw;
    height: 1.04vw;
    margin-right: 0.52vw;
  }

  &__contacts {
    margin-left: auto;
  }
}
